import Vue from 'vue';
import Router from 'vue-router';

import vContacts from '../components/v-contacts';
import vContactDetail from '../components/v-contact-detail';
import vContactAdd from '../components/v-contact-add';
// import vContactEdit from '../components/v-contact-edit';

Vue.use(Router);

let router = new Router({
    routes: [
        {
            path: '/',
            name: 'contacts',
            component: vContacts,
        },
        {
            path: '/contact',
            name: 'contact-detail',
            component: vContactDetail,
            props: true
        },
        {
            path: '/add-contact',
            name: 'add-contact',
            component: vContactAdd,
            props: true
        },
        // {
        //     path: '/edit-contact',
        //     name: 'edit-contact',
        //     component: vContactEdit,
        //     props: true
        // },
    ]
});

export default router;