import Vue from 'vue';
import Vuex from 'vuex';
import axios from 'axios';

Vue.use(Vuex);

let store = new Vuex.Store({
    state: {
        searchValue: '',
        contacts: []
    },
    mutations: {
        SET_SEARCH_VALUE: (state, value) => {
            state.searchValue = value;
        },
        SET_CONTACTS: (state, contacts) => {
            state.contacts = contacts;
        }
    },
    actions: {
        GET_CONTACTS({commit}){
            return axios('http://localhost:3000/contacts', {
                method: "GET"
            })
            .then((contacts)=>{
                commit('SET_CONTACTS', contacts.data);
                return contacts;
            })
            .catch((error)=>{
                return error;
            });
        },
        ADD_CONTACT({commit}, new_contact){
            return axios.post('http://localhost:3000/contacts', new_contact)
            .then((contacts)=>{
                commit('SET_CONTACTS', contacts.data);
                return contacts;
            })
            .catch((error)=>{
                return error;
            });
        },
        DELETE_CONTACT({commit}, id){
            return axios.delete('http://localhost:3000/contacts/' + id)
            .then((contacts)=>{
                commit('SET_CONTACTS', contacts.data);
                return contacts;
            })
            .catch((error)=>{
                return error;
            });
        },
        SEARCH_CONTACT({commit}, value){
            commit('SET_SEARCH_VALUE', value);
        },
    },
    getters: {
        SEARCH_VALUE(state){
            return state.searchValue;
        },
        CONTACTS(state){
            return state.contacts;
        }

    },
});

export default store;
