<template>
  <div class="v-contact">
    
    <div class="container my-3 ">
        <div class="jumbotron">
            <div class="row">
                <v-contact-item
                    v-for="contact in contactList"
                    :key="contact.id"
                    :contact_data="contact"
                />
            </div>
        </div>
        
    </div>
    
  </div>
</template>

<script>
import vContactItem from './v-contact-item'
import {mapActions, mapGetters} from 'vuex'


export default {
  name: 'v-contact',
  components: {
    vContactItem
  },
  data() {
      return {
          contacts: []
      }
  },
  computed: {
      ...mapGetters([
          'CONTACTS',
          'SEARCH_VALUE'
      ]),
      contactList(){
          if(this.contacts.length){
              return this.contacts;
          }else{
              return this.CONTACTS
          }
          
      }
  },
  methods: {
      ...mapActions([
          'GET_CONTACTS'
      ]),
      sortContactBySearchValue(value) {
          this.contacts = [...this.CONTACTS];
          if(value){
            this.contacts = this.contacts.filter(function(item){
                return item.name.toLowerCase().includes(value.toLowerCase());
            });
          }else{
              this.contacts = this.CONTACTS;
          }

          
      }
  },
  watch: {
      SEARCH_VALUE() {
          this.sortContactBySearchValue(this.SEARCH_VALUE);
      }
  },
  updated() {
      this.GET_CONTACTS()
      .then((responce)=>{
          if(responce.data){
              this.sortContactBySearchValue(this.SEARCH_VALUE);
          }
      });
  },
  mounted() {
      this.GET_CONTACTS()
      .then((responce)=>{
          if(responce.data){
              this.sortContactBySearchValue(this.SEARCH_VALUE);
          }
      });
  }
}
</script>

