<template>
  <div class="v-contact-add">
    <div class="container my-3 ">
        <div class="jumbotron">
            <div class="card">
                <div class="card-header h5 text-dark">
                    Add Contact
                </div>
                <div class="card-body">
                    <div class="row">
                        <div class="col-md-2 offset-1 d-flex justify-content-center align-items-center">
                            <img class="img-fluid" src="../assets/img/user.png" alt="">
                        </div>
                        <div class="col-md-7 offset-1">
                            <form>
                                <div class="form-row">
                                    <div class="form-group col-md-6">
                                        <label for="name">Name</label>
                                        <input type="text" class="form-control input" id="name" v-model="Contact.name" placeholder="Enter your name">
                                    </div>
                                    <div class="form-group col-md-6">
                                        <label for="last-name">Last Name</label>
                                        <input type="text" class="form-control" id="last-name" v-model="Contact.last_name" placeholder="Enter your last name">
                                    </div>
                                </div>
                                <div class="form-row">
                                    <div class="form-group col-md-6">
                                        <label>Phone</label>
                                        <input type="text" class="form-control phone mb-2" v-model="Contact.phone.mobile" placeholder="Mobile number">
                                        <input type="text" class="form-control phone mb-2" v-model="Contact.phone.work" placeholder="Work number">
                                        <input type="text" class="form-control phone mb-2" v-model="Contact.phone.home" placeholder="Home number">
                                    </div>
                                    <div class="form-group col-md-6">
                                        <label>Email</label>
                                        <input type="text" class="form-control email mb-2" v-model="Contact.email.email1" 
                                        placeholder="E-mail">
                                        <input type="text" class="form-control email mb-2" v-model="Contact.email.email2" placeholder="E-mail">
                                        <input type="text" class="form-control email mb-2" v-model="Contact.email.email3" placeholder="E-mail">
                                    </div>
                                </div>
                                <div class="form-group">
                                    <label>Address</label>
                                    <input type="text" class="form-control address mb-2" v-model="Contact.address.address1" placeholder="Address">
                                    <input type="text" class="form-control address mb-2" v-model="Contact.address.address2" placeholder="Address">
                                    <input type="text" class="form-control address mb-2" v-model="Contact.address.address3" placeholder="Address">
                                </div>
                                <router-link :to="{name: 'contacts'}">
                                    <button class="btn btn-outline-secondary mt-5 float-right" @click="addContact">
                                        Add contact
                                    </button>
                                </router-link>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
  </div>
  
</template>

<script>
import { mapActions } from 'vuex';
// import Axios from 'axios';


export default {
    name: 'v-contact-add',
    data() {
        return {
            Contact: {
                id: '',
                name: '',
                last_name: '',
                phone: {
                    mobile: '',
                    work: '',
                    home: '',
                },
                email: {
                    email1: '',
                    email2: '',
                    email3: '',
                },
                address: {
                    address1: '',
                    address2: '',
                    address3: ''
                }

            }
        }
    },
    methods: {
        ...mapActions([
            'ADD_CONTACT'
        ]),
        addContact() {
            let newContact = {
                id: this.Contact.id,
                name: this.Contact.name,
                last_name: this.Contact.last_name,
                phone: this.Contact.phone,
                email: this.Contact.email,
                address: this.Contact.address
            }

            this.ADD_CONTACT(newContact)
            .then(()=>{
                this.Contact.name = '';
                this.Contact.last_name = '';
                this.Contact.phone.mobile = '';
                this.Contact.phone.work = '';
                this.Contact.phone.home = '';
                this.Contact.email.email1 = '';
                this.Contact.email.email2 = '';
                this.Contact.email.email3 = '';
                this.Contact.address.address1 = '';
                this.Contact.address.address2 = '';
                this.Contact.address.address3 = '';
            });
            
            
        }
    }
  
}
</script>

<style>

</style>
