<template>
  <div class="v-main-wrapper">
    <div class="navbar navbar-dark shadow-sm">
        <div class="container d-flex justify-content-between">
            <router-link class="text-decoration-none" :to="{name: 'contacts'}">
                <div class="navbar-brand d-flex align-items-center">
                    <img src="../assets/img/contacts.png" alt="">
                    <strong class="h3 m-0 p-0">My contacts</strong>
                </div>
            </router-link>
            <form class="form-inline mt-2 mt-md-0">
                <input class="form-control mr-sm-2" type="text" placeholder="Search" aria-label="Search" v-model="searchText">
                <button class="btn btn-outline-light my-2 my-sm-0 mr-2" 
                type="submit" @click="searchContact(searchText)">Search</button>
                <button class="btn btn-outline-light my-2 my-sm-0" 
                type="submit" @click="clearSearchField">Clear</button>
            </form>
            <router-link class="text-decoration-none" :to="{name: 'add-contact'}">
                <div class="add-contact" role="button">
                    <img src="../assets/img/plus.png" title="Add contact">
                </div>
            </router-link>
        </div>
    </div>
    <keep-alive>
        <router-view></router-view>
    </keep-alive>
    
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex';

export default {
  name: 'v-main-wrapper',
  data(){
      return {
          searchText: ''
      }
  },
  computed: {
      ...mapGetters([
          'SEARCH_VALUE'
      ])
  },
  methods: {
      ...mapActions([
          'SEARCH_CONTACT'
      ]),
      searchContact(value) {
          this.SEARCH_CONTACT(value);
      },
      clearSearchField(){
          this.searchText = '';
          this.SEARCH_CONTACT();
      }
  }
}
</script>

<style>
.navbar{
    background-color: var(--purple);
}
</style>
