<template>
  <div class="v-contact-detail">
    
    <div class="container my-3 ">
        <div class="jumbotron">
            <div class="card">
                <div class="card-header h5 text-dark">
                    Contact-info
                </div>
                <div class="card-body">
                    <div class="row">
                        <div class="col-md-2 offset-1 d-flex justify-content-center align-items-center">
                            <img class="img-fluid" src="../assets/img/user.png" alt="">
                        </div>
                        <div class="col-md-7 offset-1">
                            <form>
                                <div class="form-row">
                                    <div class="form-group col-md-6">
                                        <label for="name">First Name</label>
                                        <h5 class="card-title">{{contact_data.name}}</h5>
                                    </div>
                                    <div class="form-group col-md-6">
                                        <label for="last_name">Last Name</label>
                                        <h5 class="card-title">{{contact_data.last_name}}</h5>
                                    </div>
                                </div>
                                <div class="form-row">
                                    <div class="form-group col-md-6">
                                        <label for="phone">Phone</label>
                                        <h5 class="card-title" 
                                            v-for="phone in contact_data.phone"
                                            :key="phone.id"
                                        >
                                            {{phone}}
                                        </h5>
                                    </div>
                                    <div class="form-group col-md-6">
                                        <label for="email">E-mail</label>
                                        <h5 class="card-title" 
                                            v-for="email in contact_data.email"
                                            :key="email.id"
                                        >
                                            {{email}}
                                        </h5>
                                    </div>
                                </div>
                                <div class="form-row">
                                    <div class="form-group col-md-12">
                                        <label for="address">Address</label>
                                        <h5 class="card-title"
                                              v-for="address in contact_data.address"
                                              :key="address.id"
                                        >
                                            {{address}}
                                            
                                        </h5>
                                    </div>
                                </div>
                                <button class="btn btn-outline-secondary mr-3">
                                    Edit contact
                                </button>
                                
                                <router-link :to="{name: 'contacts'}">
                                    <button class="btn btn-outline-secondary" @click="deleteContact(contact_data)">
                                        Delete contact
                                    </button>
                                </router-link>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
  </div>
</template>

<script>
import { mapActions } from 'vuex';

export default {
  name: 'v-contact-detail',
  props: {
    contact_data: {
        type: Object,
        default(){
            return {}
        }
    }
  },
  methods: {
      ...mapActions([
          'DELETE_CONTACT'
      ]),
      deleteContact(contact_data){
          this.DELETE_CONTACT(contact_data.id)
      }
  },
}
</script>

<style>

</style>
